<template>
    <div class="map-style">
        <div class="charts-style" :id="id"></div>
    </div>
</template>

<script>
    import echarts from 'echarts';
    import 'echarts-gl';

    export default {
        name: "remoteWorkMap",
        data() {
            return {
                id: Math.random() + '_'
            }
        },
        watch: {},
        created() {
        },
        mounted() {
            this.init()
        },
        methods: {
            init(){
                let chartsDom = document.getElementById(this.id);
                let charts = echarts.init(chartsDom);
               this.$axios.get('static/data/map.json').then(res=>{
                   echarts.registerMap('cq', res.data);
                   let option = {
                       tooltip: {
                           show: true,
                       },
                       visualMap: [],
                       geo3D: {
                           map: 'cq',
                           roam: true,
                           itemStyle: {
                               areaColor: '#1149a3',
                               opacity: 1,
                               borderWidth: 0.8,
                               borderColor: '#5ca2f9'
                           },
                           label: {
                               show: true,
                               textStyle: {
                                   color: '#fff', //地图初始化区域字体颜色
                                   fontSize: 16,
                                   opacity: 1,
                                   backgroundColor: 'rgba(0,0,0,0)'
                                   //backgroundColor: 'rgba(53,171,199,0)'
                               },
                           },
                           emphasis: { //当鼠标放上去  地区区域是否显示名称
                               label: {
                                   show: true,
                                   textStyle: {
                                       color: '#fff',
                                       fontSize: 16,
                                       backgroundColor: '#5ca2f9'
                                   }
                               }
                           },
                           //shading: 'lambert',
                           light: { //光照阴影
                               main: {
                                   color: '#fff', //光照颜色
                                   intensity: 1.4, //光照强度
                                   //shadowQuality: 'high', //阴影亮度
                                   shadow: false, //是否显示阴影
                                   alpha: 55,
                                   beta: 10

                               },
                               ambient: {
                                   intensity: 0.3
                               }
                           },
                       },
                       series: [

                           //画线


                       ]
                   };
                   charts.setOption(option)

               })

            }
        }
    }
</script>

<style scoped lang="scss">
    .map-style{
        width: 100%;
        height: 100%;
        position: relative;
        .charts-style{
            width: 100%;
            height: 100%;
        }
    }
</style>